
import styles from './LightBall.module.css'

type LightBallProps = {
  x: string;
  y: string;
  width: string;
  height: string;
}

function LightBall( {x, y, width, height} : LightBallProps) {
  return (
    <div style={{left: x, bottom: y, width: width, height: height}} className={styles.lightBall}>
      <div className={styles.light1}></div>
      <div className={styles.light2}></div>
      <div className={styles.light3}></div>
      <div className={styles.light4}></div>
      <div className={styles.light5}></div>
    </div>
  )
}

export default LightBall;